.promotions {
  width: 100%;
  padding: 102px 60px 115px;

  @include vp-1199 {
    padding: 87px 45px 30px;
  }

  @include vp-767 {
    padding: 15px 15px 50px;
  }
}

.promotions__wrapper {
  max-width: 1160px;
  margin: 0 auto;
}

.promotions__title {
  font-family: "AlumniSans", "Arial", sans-serif;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  color: #102654;
  text-align: center;
  margin: 0 0 34px 0;

  @include vp-1199 {
    font-size: 42px;
    line-height: 50px;
    margin: 0 9px 43px 0;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 38px;
    margin: 0 5px 42px 0;
  }
}

.promotions__container {
  display: grid;
  grid-template-columns: repeat(2, minmax(auto, 561px));
  grid-template-rows: repeat(auto-fit, auto);
  gap: 40px;

  @include vp-1199 {
    grid-template-columns: repeat(1, auto);
    grid-template-rows: auto auto;
    gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: 100%;
  }
}

.promotions__principal-container {
  display: grid;
  grid-template-columns: repeat(2, auto);
  background-color: #ed0233;
  justify-content: space-between;

  @include vp-1199 {
    grid-column: 1/-1;
  }

  @include vp-767 {
    grid-template-columns: repeat(1, 100%);
    grid-template-rows: repeat(2, auto);
  }
}

.promotions__content {
  display: flex;
  flex-direction: column;
  padding: 34px 46px 45px 45px;

  @include vp-1199 {
    padding: 36px 44px 44px 45px;
  }

  @include vp-767 {
    padding: 37px 50px 44px;
    align-items: center;
  }
}

.promotions__content-year {
  font-family: "RobotoCondensed-Bold", "Arial", sans-serif;
  font-size: 28px;
  line-height: 156%;
  color: #ffffff;
  margin: 0;

  @include vp-1199 {
    margin: -2px 0 0 0;
  }
}

.promotions__content span {
  font-family: "RobotoCondensed-Bold", "Arial", sans-serif;
  font-size: 70px;
  line-height: 156%;
  color: #ffffff;
  margin: -26px 0 0 0;
}

.promotions__content p:not([class]) {
  font-size: 18px;
  line-height: 140%;
  color: #ffffff;
  max-width: 190px;
  margin: 0;

  @include vp-767 {
    margin: -6px 0 25px;
    text-align: center;
  }
}

.promotions__content-button {
  height: auto;
  margin: auto 0 0;
}

.promotions__button {
  font-family: "RobotoCondensed-Bold", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border-bottom: 1px solid #ffffff;
  padding: 10px 0 1px;
  margin: 0 0 6px;
  cursor: pointer;
  user-select: none;

  &:hover,
  &:focus,
  &:active {
    text-shadow: 0.1px 0.1px 5px #ffffff;
  }

  @include vp-767 {
    position: inherit;
  }
}

.promotions__list {
  grid-column: 2;
  grid-row: 1/-1;
  display: grid;
  grid-template-rows: repeat(auto-fit, auto);
  grid-template-columns: repeat(1, 100%);
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style-type: none;
  gap: 40px;

  @include vp-1199 {
    grid-column: 1/-1;
    grid-row: 2;
    grid-template-columns: repeat(2, 47.8%);
    justify-content: stretch;
    gap: 30px;
  }

  @include vp-767 {
    grid-template-rows: repeat(auto-fit, auto);
    grid-template-columns: repeat(1, 100%);
  }
}

.promotions__item {
  display: flex;
  flex-direction: column;
  padding: 26px 20px 28px 27px;
  border: 7px solid;
  border-image: linear-gradient(148.82deg, #d10a42 0.85%, #00359f 100%) 1;
  box-sizing: border-box;

  @include vp-1199 {
    padding: 26px 34px 28px 28px;
    width: 100%;
    justify-content: space-between;
  }

  @include vp-767 {
    padding: 36px 37px 39px 40px;
    align-items: center;
    width: 100%;
  }
}

.promotions__item h4 {
  font-family: "RobotoCondensed-Bold", "Arial", sans-serif;
  font-size: 26px;
  color: #1c3374;
  margin: 0;

  @include vp-767 {
    text-align: center;
  }
}

.promotions__item p {
  font-size: 16px;
  color: #1c3374;
  margin: 9px 0 7px;

  @include vp-1199 {
    margin: 12px 0 48px;
    line-height: 22px;
    width: 163px;
  }

  @include vp-767 {
    margin: 12px 0 27px;
    text-align: center;
  }
}

.promotions__item:last-child p {
  @include vp-1199 {
    margin: 10px 0 5px;
    width: 100%;
  }

  @include vp-767 {
    margin: 14px 1px 27px 0;
  }
}

.promotions__item-button {
  font-family: "RobotoCondensed-Bold", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #1c3374;
  border-bottom: 1px solid #1c3374;
  align-self: flex-start;
  cursor: pointer;
  user-select: none;
  margin: auto 0 0;

  &:hover,
  &:focus,
  &:active {
    text-shadow: 0.1px 0.1px 1px #1c3374;
  }

  @include vp-767 {
    align-self: center;
  }
}

.promotions__picture img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  background-color: #4f86e5;
}
